*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;

  // align-items: center;
  // justify-content: center;
  min-height: 100vh;
  margin: 0;
  padding: 30px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  background-color: #fff;
  color: #212121;
}

/* Lightbox */
.lightbox {
  /* display: flex;
  align-items: center;
  justify-content: center; */
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  pointer-events: none;
  /* transition: opacity var(--animation-duration) var(--timing-function); */
}

.lightbox.is-open {
  opacity: 1;
  pointer-events: initial;
}
.lightbox__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
}
.lightbox__content label {
  color: #fff;
}
.lightbox__content table {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
}
.lightbox__button {
  position: absolute;
  top: 8px;
  right: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 50%;
  background-color: transparent;
  color: #fff;
  cursor: pointer;
  transition: background-color var(--animation-duration) var(--timing-function);
  outline: none;
  background-image: url('../images/icon-close.svg');
  background-position: center;
  background-size: 60%;
  background-repeat: no-repeat;
}
.note-form {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  overflow: hidden;
  pointer-events: none;
}

// notes table
.notes-table,
.summary-table,
.archive-table {
  width: 100%;
  border-collapse: collapse;
  text-align: left;
  font-size: 20px;
}
.table__head {
  height: 50px;
  border-bottom: 5px solid #ffffff;
  border-left: 5px solid #757575;
  color: #fff;
  background-color: #757575;
}
.notes-row,
.summary-row,
.archive-row {
  height: 75px;
  border-bottom: 5px solid #ffffff;
  border-left: 5px solid #94bfd8;
  color: #212121;
  background-color: #94bfd8;
}
.create__button {
  padding: 10px;
  margin: 10px 0 10px auto;
  border-radius: 3px;
  font-size: 24px;
  font-weight: 600;
  cursor: pointer;
}
.archive-table {
  width: 50%;

}
.note-form {
  width: 300px;
  font-size: 24px;
}
.note-form label{
  margin-bottom: 10px;
}
.note-form input {
  width: 300px;
}
.note-form button {
  width: 300px;
  padding: 10px 0;
  margin: 10px 0 10px auto;
  border-radius: 3px;
  font-weight: 600;
  cursor: pointer;
}
td, th {
  padding: 10px;
}